.dotBar{
  position: absolute;
  display: flex;
  flex-direction: column;
  height: 20rem;
  width: 5rem;
  right: 10%;
  z-index: 6;
  align-items: center;
  justify-content: space-evenly;
}

.dotLine{
  position: absolute;
  height: 200px;
  width: 1px;
  background-color: var(--light);
  filter: opacity(0);
}

.dot{
  width: 1rem;
  height: 1rem;
  /* border-radius: 0.75rem; */
  background-color: var(--light);
  filter: opacity(0.5);
  transform: scale(0.5) rotate(45deg);
  z-index: 2;
  transition: 0.5s;
  transition-delay: 0.0s;
}

.dotSelected{
  width: 1rem;
  height: 1rem;
  background-color: var(--light);
  filter: opacity(1) drop-shadow(0px 0px 10px white) drop-shadow(0px 0px 10px white) drop-shadow(0px 0px 10px white);
  transform: scale(1) rotate(45deg);
  z-index: 2;
  margin-right: 0rem;
}